<template>
  <el-container>
    <el-header>
      <h1 style="color:#fff">
        Spark's Blog!
      </h1>
    </el-header>
    <el-container>
      <el-aside width="240px">
        <div style="color:#fff;padding:20px 0;font-size:20px;border-bottom:0.5px solid #ccc">
          文章分类
          <br>
          Catalog
        </div>
        <el-menu background-color="transparent" text-color="#fff" style="width:100%">
       <el-menu-item index="1" @click="filterCatalog($event)">
        <span slot="title">All</span>
      </el-menu-item>
      <el-submenu index="2">
        <template slot="title">
          <span>Front-End</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="2-1" @click="filterCatalog($event)">HTML</el-menu-item>
          <el-menu-item index="2-2" @click="filterCatalog($event)">CSS</el-menu-item>
          <el-menu-item index="2-3" @click="filterCatalog($event)">JavaScript</el-menu-item>
          <el-menu-item index="2-4" @click="filterCatalog($event)">Framework</el-menu-item>          
        </el-menu-item-group>
      </el-submenu>
      <el-menu-item index="3" @click="filterCatalog($event)">
        <span slot="title">Others</span>
      </el-menu-item>
      <el-menu-item index="4" @click="filterCatalog($event)">
        <span slot="title">Free</span>
      </el-menu-item>
    </el-menu>
    </el-aside>
      <el-container>
      <el-main>
        <router-view/>
      </el-main>
      </el-container>
    </el-container>
    <el-footer>
     本站原创内容转载请注明来源
    </el-footer>
</el-container>
</template>

<script>
import articleList from "@/assets/article.json";
import articleBar from "@/components/articleBar";
export default {
  name: "blogHome",
  data() {
    return {
      showList: []
    };
  },
  created() {
    this.showList = JSON.parse(JSON.stringify(articleList));
    this.showList.sort(function(a, b) {
      let aArr = a.date.split("-");
      let bArr = b.date.split("-");
      let aNum = aArr[0] * 365 + aArr[1] * 31 + aArr[2];
      let bNum = bArr[0] * 365 + bArr[1] * 31 + bArr[2];
      return bNum - aNum;
    });
    this.$store.commit("changeList", this.showList);
  },
  mounted() {},
  components: {
    articleBar
  },
  methods: {
    filterCatalog(event) {
      let string = event.$el.innerText;
      let arr = [];
      if (string !== "All") {
        for (let i = 0; i < articleList.length; i++) {
          if (articleList[i].type == string) {
            arr.push(articleList[i]);
          }
        }
      } else {
        arr = articleList;
      }
      arr.sort(function(a, b) {
        let aArr = a.date.split("-");
        let bArr = b.date.split("-");
        let aNum = aArr[0] * 365 + aArr[1] * 31 + aArr[2];
        let bNum = bArr[0] * 365 + bArr[1] * 31 + bArr[2];
        return bNum - aNum;
      });
      let arrString = JSON.stringify(arr);
      sessionStorage.setItem("nowArr", arrString);
      this.$store.commit("changeList", arr);
      this.$store.commit("changeSport", false);
      setTimeout(() => {
        this.$store.commit("changeSport", true);
      }, 100);
      this.$router.push("blogCatalog");
    }
  }
};
</script>


<style scoped>
.el-header {
  background-color: #417eb7;
  color: #333;
  text-align: center;
  line-height: 70px;
  height: 200px !important;
  width: 100%;
  z-index: 1;
  background: url("../assets/title-background.jpg") no-repeat 100% 100%;
}
.el-footer {
  background-color: rgb(216, 216, 220);
  color: #333;
  text-align: center;
  line-height: 60px;
  margin-top: 50px;
}
.el-aside {
  margin-top: 20px;
  margin-left: 10%;
  text-align: center;
  color: white;
  min-width: 200px;
  min-height: 400px;
  max-height: 600px;
  overflow: hidden;
  background: -webkit-linear-gradient(
    bottom,
    rgb(18, 151, 215),
    rgb(34, 143, 204) 30%,
    rgb(65, 126, 183) 50%,
    rgb(10, 53, 137) 100%
  );
  /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(
    top,
    rgb(18, 151, 215),
    rgb(34, 143, 204) 30%,
    rgb(65, 126, 183) 50%,
    rgb(10, 53, 137) 100%
  );
  /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(
    top,
    rgb(18, 151, 215),
    rgb(34, 143, 204) 30%,
    rgb(65, 126, 183) 50%,
    rgb(10, 53, 137) 100%
  );
  /* Firefox 3.6 - 15 */
  background: linear-gradient(
    to top,
    rgb(18, 151, 215),
    rgb(34, 143, 204) 30%,
    rgb(65, 126, 183) 50%,
    rgb(10, 53, 137) 100%
  );
}

.el-main {
  margin-top: 20px;
  margin-left: 10%;
  width: 80%;
  overflow: auto;
  color: #333;
  text-align: left;
  padding: 0;
}

@media screen and (max-width: 768px) {
  .el-container {
    flex-direction: column;
  }
  .el-aside {
    width: 80% !important;
  }
}
</style>



